import React, { useEffect, useState } from "react";
import { useLocation } from "react-router-dom";
import StudentImg from "../static/images/Student.png";
import ParentImg from "../static/images/Parent.png";
import ContentCreatorImg from "../static/images/Content Creator.png";
import ArtistImg from "../static/images/Artist.png";
import CryptoCurrencyImg from "../static/images/Crypto-currency.png";
import DeveloperImg from "../static/images/Developer.png";

export default function RoleBanner() {
    const [selectedRole, setSelectedRole] = useState({
        image: null,
        bg_color: null
    });

    const { state } = useLocation();
    useEffect(() => {
        switch (state ? state.role : "") {
            case "student":
                setSelectedRole({
                    image: StudentImg,
                    bg_color: "rgb(234,250,255)",
                });
                break;
            case "parent":
                setSelectedRole({
                    image: ParentImg,
                    bg_color: "rgb(204,233,255)",
                });
                break;
            case "content_creator":
                setSelectedRole({
                    image: ContentCreatorImg,
                    bg_color: "rgb(188,255,238)",
                });
                break;
            case "developer":
                setSelectedRole({
                    image: DeveloperImg,
                    bg_color: "rgb(0,42,105)",
                });
                break;
            case "crypto":
                setSelectedRole({
                    image: CryptoCurrencyImg,
                    bg_color: "rgb(255,209,102)",
                });
                break;
            case "artist":
            default:
                setSelectedRole({
                    image: ArtistImg,
                    bg_color: "rgb(124,199,255)",
                });
                break;
        }
    }, [state]);

    if (!selectedRole.image)
        return <span></span>

    return (
        <div>
            <div style={{ background: selectedRole.bg_color }}>
                <div className="row m-0">
                    <div className="col-md-7 text-left">
                        <div
                            style={{
                                padding: "10% 20px 20px 20px",
                                color: "rgb(38,150,234)",
                            }}>
                            <h4>
                                <strong>Contact Us</strong>
                            </h4>
                            <p>
                                Lorem ipsum dolor sit amet, consectetur adipiscing elit. Nulla
                                ac id tortor lorem quis.{" "}
                            </p>
                        </div>
                    </div>
                    <div className="col-md-5">
                        <div>
                            <img src={selectedRole.image} alt="student" width="50%"
                                style={{
                                    background: "transparent",
                                    borderRadius: "5px",
                                    padding: "10px 10px 40px 10px",
                                }} />
                        </div>
                    </div>
                </div>
            </div>
            <br />
        </div>
    )
}