import React from "react";
import { Navigate, Route, Routes } from "react-router-dom";
import "../node_modules/bootstrap/dist/css/bootstrap.min.css";
import "./App.css";
import "./static/css/style.css";
import "../node_modules/jquery/dist/jquery.min.js";
import "../node_modules/bootstrap/dist/js/bootstrap.min.js";

import HeaderPage from "./HeaderPage";
import MainPage from "./MainPage";
import FooterPage from "./FooterPage";
import JoinTheHub from "./JoinTheHub";
import Student from "../src/roles/Student";
import Parent from "../src/roles/Parent";
import ContentCreator from "./roles/ContentCreator";
import Developer from "./roles/Developer";
import CryptoPage from "./roles/CryptoPage";
import Artist from "./roles/Artist";
import ContactUs from "./ContactUs";
import AboutUs from "./AboutUs";
import Careers from "./Careers";

function App() {
  return (
    <div className="App">
      <HeaderPage />
      <Routes>
        <Route path="/" element={<MainPage />} />
        <Route path="/join_the_hub" element={<JoinTheHub />} />
        <Route path="/roles/student" element={<Student />} />
        <Route path="/roles/parent" element={<Parent />} />
        <Route path="/roles/content_creator" element={<ContentCreator />} />
        <Route path="/roles/developer" element={<Developer />} />
        <Route path="/roles/crypto" element={<CryptoPage />} />
        <Route path="/roles/artist" element={<Artist />} />
        <Route path="/careers" element={<Careers />} />
        <Route path="/about_us" element={<AboutUs />} />
        <Route path="/contact_us" element={<ContactUs />} />
        <Route path="*" element={<Navigate to="/" replace />} />
      </Routes>
    </div>
  );
}

export default App;
